$font-family-sans-serif: "Kohinoor", "Open Sans", "Gill Sans MT", "Gill Sans", Corbel, Arial, sans-serif;

$body-color: #424244 !default;
$green: #6A955B !default;
$green-faded: #D1DECD;
$red: #FF422E !default;
$red-light: #FFD9D5 !default;
$red-dark: #99444F !default;
$card-border-radius: 6px;

$primary: $green;

$alert-border-width: 0;
$alert-border-radius: 6px;
$alert-padding-x: 1.5rem;
$alert-padding-y: 1.2rem;

$input-btn-padding-y:  .75rem;
$input-btn-padding-x: 3.75rem;

$toast-z-index: 1000 !default;
$toast-duration: 3000ms !default;
$toast-height: 8rem !default;
$toast-height-sm: 5.8rem !default;
$toast-breakpoint: 768px !default;