body::-webkit-scrollbar {
  display: none;
}

html.no-scroll,
html.no-scroll body {
  overflow: hidden;
  -webkit-overflow-scrolling: auto;
}

body:before {
  content: '';
  pointer-events: none;
  opacity: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 390;
  background-color: rgba(0,0,0,0.4);
  transition: all 0.3s ease-in-out;
}

html.no-scroll body:before {
  opacity: 1;
}

html.no-scroll.block-all body:before {
  z-index: 10000;
}
