[tooltip-danger] {
  position: relative;

  &:before {
    content: attr(tooltip-danger);
    position: absolute;
    display: block;
    transform: translateY(-100%) translateX(-50%);
    left: 50%;
    width: 15em;
    font-size: 0.75em;
    background: $red-light;
    padding: 0.7em 1em;
    border-radius: 0.4rem;
    top: -1em;
    pointer-events: none;
    color: $red-dark;
    font-weight: 600;
  }

  &:after {
    top: -0.8em;
    left: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: rgba(213, 0, 0, 0);
    border-width: 8px;
    margin-left: -8px;
    border-top-color: $red-light;
  }

  &:before, &:after {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  &:hover {
    &:before, &:after {
      opacity: 1;
    }
  }
}