input:focus {
  outline: none;
}

.form-group-icon {
  display: flex;
  align-items: center;
  position: relative;

  img {
    width: 1.5em;
    position: absolute;
    left: -2.5em;
  }
}

.form-group {
  label {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 0.875rem;
    margin-bottom: 0;
  }
}

.visibility-toggle {
  position: relative;

  .form-control {
    padding-right: 3em;
  }

  button {
    border: none;
    background: none;
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 3em;

    svg {
      width: 1.5em;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      
      path {
        fill: $body-color;
      }
    }
  }

  &--on {
    button svg path {
      fill: $green;
    }
  }
}

.form-control {
  color: $body-color;
  padding: 0.8em 1.2em;
}

.icon-form-wrapper {
  @media screen and (max-width: map-get($grid-breakpoints, sm)) {
    padding-left: 4rem;
  }
}

