.input-with-check-wrapper {
  position: relative;
  
  input {
    padding-right: 2em;
    position: relative;
  }

  &.input-with-check-wrapper--checked:after {
    content:  url(data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20viewBox%3D%220%200%2017.5%2014%22%3E%3Cpath%20d%3D%22M1.1%207.3l4.6%204.6L16.5%201.1%22%20fill%3D%22none%22%20stroke%3D%22%236a955b%22%20stroke-width%3D%223%22/%3E%3C/svg%3E);
    position: absolute;
    width: 1.5em;
    right: 1em;
    top: 57%;
    transform: translateY(-50%);
  }
}

.input-with-search-wrapper {
  position: relative;

  input {
    padding-left: 2.5em;
  }
  
