.spinner {
  font-size: 2rem;
  width: 1em;
  height: 1em;
  clear: both;
  margin: 0.625em auto;
  border: 0.125em rgba($body-color,0.25) solid;
  border-top: 0.125em rgba($body-color,1) solid;
  border-radius: 50%;
  animation: spCircRot .6s infinite linear;

  &.light {
    border-color: rgba(255,255,255,0.25);
    border-top-color: rgba(255,255,255,1);
  }
}

@keyframes spCircRot {
  from { transform: rotate(0deg); }
  to { transform: rotate(359deg); }
}