@font-face {
  font-family: "Kohinoor";
  font-weight: 300;
  font-display: fallback;
  src: url("/assets/fonts/Kohinoor-Light.woff") format("woff");
}

@font-face {
  font-family: "Kohinoor";
  font-weight: 400;
  font-display: fallback;
  src: url("/assets/fonts/Kohinoor-Book.woff") format("woff");
}

@font-face {
  font-family: "Kohinoor";
  font-weight: 500;
  font-display: fallback;
  src: url("/assets/fonts/Kohinoor-Demi.woff") format("woff");
}

@font-face {
  font-family: "Kohinoor";
  font-weight: 600;
  font-display: fallback;
  src: url("/assets/fonts/Kohinoor-Medium.woff") format("woff");
}

@font-face {
  font-family: "Kohinoor";
  font-weight: 700;
  font-display: fallback;
  src: url("/assets/fonts/Kohinoor-Bold.woff") format("woff");
}

a {
  cursor: pointer;

  &:hover,
  a:not([href]),
  a:not([href]):not([tabindex]),
  &:not([href]):not([tabindex]):hover,
  &:not([href]):not([tabindex]):focus, {
    text-decoration: underline;
  }
}