.toast {
  position: fixed;
  background-color: $green-faded;
  color: $green;
  z-index: $toast-z-index;
  height: $toast-height;
  width: 100%;
  transform: translateY(-100%);
  transition: all 0.5s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.125rem;
  font-weight: 600;
  animation: toast $toast-duration ease-in-out;
  animation-delay: 200ms;

  svg {
    width: 1.5em;
    margin-right: 1em;
    margin-top: -0.2em;

    path {
      stroke: $green;
    }
  }

  @media screen and (max-width: #{$toast-breakpoint}) {
    height: $toast-height-sm;
    font-size: 1rem;
  }
}

@keyframes toast {
  0% {
    transform: translateY(-100%);
  }

  20% {
    transform: translateY(0);
  }

  90% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-100%);
  }
}