html {
  font-display: swap;
}
@font-face {
  font-family: 'GL-Nummernschild';
  src: url('/assets/fonts/GL-Nummernschild-Eng.woff') format('woff');
}

.app-content-area h1 {
  font-size: 1.125rem;
  margin-bottom: 1em;
  font-weight: 600;
}

.btn-primary {
color: #fff;
}

.grey-content-area {
  background-color: $search-grey;
  &.community-posts {
    app-header .function-bar {
      box-shadow: 2px 2px 5px $gray-300;
      position: relative;
      z-index: 9;
    }
    .app-content-area.app-content-area {
      margin: -2.5rem 0 0 7.9rem;
      @media (max-width: 768px) {
        margin: 0rem;
        padding: 1rem;
      }
    }

    app-nav {
      &::before {
        box-shadow: 2px 2px 5px $gray-400;
      }
    }
    .app-nav {
      top: 3.3rem;
    }
  }
}
