.noUi-target {
  border: none;
  box-shadow: none;
  background-color: #E5E5E5;
  border-radius: 15px;
  margin-left: 8px;
  margin-right: 16px;
}

.noUi-connect {
  background-color: $green;
}

html:not([dir=rtl]) .noUi-horizontal .noUi-handle {
  box-shadow: none;
  height: 25px;
  width: 25px;
  border-radius: 25px;
  border-color: #BCBCBC;
  margin-top: 0.1em;
  cursor: -webkit-grab;
  transition: all 0.2s ease-in-out;

  &:before, &:after {
    content: none;
  }

  &:active, &:active:hover {
    cursor: -webkit-grabbing;
    box-shadow: 0 0 0px 5px rgba(106, 149, 90, 0.41);
  }

  &:hover {
    box-shadow: 0 0 0px 3px rgba(106, 149, 90, 0.25);
  }

  &:focus {
    outline: none;
  }
}
