@import '../../node_modules/bootstrap/scss/functions';
@import '../../node_modules/@taropumps/bootstrap/scss/variables';
@import '../../node_modules/bootstrap/scss/variables';

$nav-grey: #e2e2e2;
$search-grey: #f2f2f2;
$header-padding: 2.5rem;
$header-padding-sm: 1rem;
$base-nav-width: 7.9em;
$expanded-nav-width: 20em;
$header-height: 12.4rem;
$menu-transition: all 0.2s ease-out;
$function-bar-button-padding: 1rem 3.3rem;
$function-bar-button-padding-sm: 1rem 1.7rem;
$content-area-color: #fff;
$warning: #EA0029;


@media print {
  .app-header,
  .function-bar,
  app-section-header,
  .media-none {
    display: none !important;
  }
  .app-content-area {
    margin: 0 !important;
    background-color: $content-area-color;
  }
  .media-print {
    flex: 0 0 100% !important;
    max-width: 100% !important;
  }
}
