.taro-material-datepicker.taro-material-datepicker {
  position: relative;

  input {
    background-color: #f2f2f2;
    background-image: none;
    pointer-events: none;
  }
}

mat-datepicker-toggle {
  position: absolute;
  right: 0.5em;
  top: 50%;
  transform: translateY(-50%);

  .mat-icon-button {
    background-color: #6c747d;
  }
  svg {
    color: #fff;
  }
}

.taro-material-date-range-picker {
  position: relative;
  color: #424244;
  padding: 0.75rem;
  background: #f2f2f2;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;

  input {
    pointer-events: none;
  }

  &__clear {
    position: absolute;
    right: 3.2em;
    top: 0.7em;
    border: none;
    background: transparent;

    svg {
      width: 0.8em;
    }
  }
}
