.mat-table.mat-table {
  font-family: 'Kohinoor', 'Open Sans', 'Gill Sans MT', 'Gill Sans', Corbel,
    Arial, sans-serif;
  color: #424244;

  th.mat-header-cell,
  td.mat-cell,
  td.mat-footer-cell {
    border: none;
  }
}

.taro-table.taro-table.taro-table.taro-table {
  width: 100%;

  th {
    padding: 1em;
  }

  th,
  th button {
    text-transform: uppercase;
    font-size: 0.875rem;
    font-weight: 700;
  }

  tbody {
    td.mat-cell {
      font-size: 1em;
      background-color: $search-grey;
      border-bottom: 5px solid white;
      padding: 1em;
      position: relative;
      height: 4.25em;

      &:before {
        content: '';
        position: absolute;
        background: #c4c4c5;
        width: 1px;
        height: calc(100% - 2em);
        right: 0;
        top: 1em;
      }

      &:last-child:before, &.no-seperator:before {
        display: none;
      }
    }
  }

  button.row-toggle.row-toggle {
    position: absolute;
    padding-right: 1em;
    top: 0;
    right: 0;
    height: 100%;
    transition: all 0.2s ease-in-out;
    background: rgba(0, 0, 0, 0.05);
    padding-top: 0.8em;
    padding-bottom: 0.9em;
    padding-left: 1em;

    &:before {
      content: none;
    }
  }

  svg.circleToggle {
    fill: #fff;
  }

  button.row-toggle.ellipsis {
    > div {
      background-color: #424244;
      padding: 0.2em;
      padding-top: 0.6em;
    }

    &:hover > div {
      background-color: black;
    }
  }
}
