@import "globalvariables";

.form-control {
  background-color: #f2f2f2;
  font-size: 1em;
}
.form-control:disabled {
  cursor: default;
  background-color: rgba(234,234,234, 0.58) !important;
  background-image: none !important;
  opacity: 0.5;
}
.form-control:read-only {
  cursor: default;
  background-color: rgba(234,234,234, 0.58);
}
.custom-select {
  color: $body-color;
  padding: 0.75rem 1.75rem 0.75rem 0.75rem;
  background: $search-grey
    url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='4' height='5' viewBox='0 0 4 5'%3e%3cpath fill='%23343a40' d='M2 0L0 2h4zm0 5L0 3h4z'/%3e%3c/svg%3e")
    no-repeat right 0.75rem center/8px 10px;
}

.form-group {
  label {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 0.875em;
    margin-bottom: 0;
  }
}

.form-actions {
  background-color: $search-grey;
  position: fixed;
  bottom: 0;
  width: calc(100% - #{$base-nav-width});
  padding: 2rem;
  padding-left: 2rem;
  left: $base-nav-width;
  transform: translateY(100%);
  transition: $menu-transition;
  box-shadow: 0 0 3px #c3c3c3;
  z-index: 300;

  &.visible {
    transform: translateY(0);
  }

  .nav-expanded & {
    left: $expanded-nav-width;
    width: calc(100% - #{$expanded-nav-width});
  }

  .btn {
    .spinner {
      margin: 0 auto;
      font-size: 1.5rem;
    }

    img {
      width: 1.5em;
      margin-top: -0.3em;
      margin-right: 0.4em;
    }
  }

  @media screen and (max-width: 800px) {
    left: 0;
    width: 100%;
    z-index: 80;

    .nav-expanded & {
      left: 0;
      width: 100%;
    }
  }
}

label.taro-checkbox,
label.taro-radio-check {
  overflow: hidden;
  position: relative;
  cursor: pointer;
  width: 1.6rem;
  height: 1.6rem;
  margin-bottom: 0;
  user-select: none;

  input[type='checkbox'],
  input[type='radio'] {
    position: absolute;
    left: -100em;

    + div {
      width: 1.5rem;
      height: 1.5rem;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);

      &:before {
        content: url(data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20viewBox%3D%220%200%2024%2024%22%3E%0A%3Cpath%20fill%3D%22%23fff%22%20stroke%3D%22%23e5e5e5%22%20d%3D%22M.5.5h23v23H.5z%22%20data-name%3D%22rect%22/%3E%0A%3Cpath%20fill%3D%22none%22%20stroke%3D%22%23F2F2F2%22%20stroke-width%3D%223%22%20d%3D%22M4.5%2012.786l4.553%204.553L19.893%206.5%22%20data-name%3D%22tick%22/%3E%0A%3C/svg%3E);
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
      }
    }

    &:checked + div:before {
      content: url(data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20viewBox%3D%220%200%2024%2024%22%3E%0A%3Cpath%20fill%3D%22%23fff%22%20stroke%3D%22%23e5e5e5%22%20d%3D%22M.5.5h23v23H.5z%22%20data-name%3D%22rect%22/%3E%0A%3Cpath%20fill%3D%22none%22%20stroke%3D%22%236A955B%22%20stroke-width%3D%223%22%20d%3D%22M4.5%2012.786l4.553%204.553L19.893%206.5%22%20data-name%3D%22tick%22/%3E%0A%3C/svg%3E);
    }

    &:focus {
      + div:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        box-shadow: inset 0 0 0 2px $green;
      }
    }
  }

  &.inline-label {
    width: auto;

    span {
      margin-top: 0.2em;
    }

    input[type='checkbox'] + div,
    input[type='radio'] + div {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      position: relative;

      + span {
        margin-left: 0.5em;
      }
    }
  }
}

