.btn .spinner {
  margin: 0 auto;
  font-size: 1.5rem;
}

.btn.add-btn {
  background-color: transparent;
  color: $body-color;
  padding: 0.75rem 1.8rem;

  span {
    text-decoration: underline;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      background: url(data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20viewBox%3D%220%200%2019%2019%22%3E%3Cg%20transform%3D%22translate%28-969%20-519%29%22%3E%3Ccircle%20cx%3D%229.5%22%20cy%3D%229.5%22%20r%3D%229.5%22%20fill%3D%22%236a955b%22%20transform%3D%22translate%28969%20519%29%22/%3E%3Cpath%20fill%3D%22%23fff%22%20d%3D%22M975%20529.59h2.8v2.82h1.8v-2.82h2.81v-1.77h-2.805V525h-1.8v2.82H975z%22/%3E%3C/g%3E%3C/svg%3E%0A);
      background-size: contain;
      background-repeat: no-repeat;
      width: 1.4em;
      height: 1.4em;
      left: -1.9em;
      top: -0.3em;
    }
  }

  &:hover {
    span {
      text-decoration: none;
    }
  }
}

.grey-box {
  background-color: #f2f2f2;
  padding: 1em;
}

.file-drop-zone {
  padding: 0.5em;
  min-height: 15em;
  text-align: center;
  position: relative;
  overflow: hidden;
  border: 1px solid #dadada;
  background-color: #fafafa;
  transition: all 0.3s ease-in-out;

  &:before {
    content: '';
    position: absolute;
    left: 1em;
    top: 1em;
    transition: all 0.3s ease-in-out;
    height: calc(100% - 2em);
    width: calc(100% - 2em);
    border-radius: 1em;
    border: 2px dashed #dadada;
  }

  &:hover {
    &:before {
      border: 2px dashed #929292;
    }
  }

  &__inner {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 1em;
    width: calc(100% - 2em);

    .file-drop-zone__upload-icon {
      display: block;
      margin: 0 auto;
      width: 5em;
      margin-bottom: 1em;
    }
  }

  .cover-btn {
    position: absolute;
    opacity: 0;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    cursor: pointer;
  }

  &.drag-over {
    &:before {
      border-color: $success;
      background-color: rgba($success, 0.11);
      box-shadow: inset 0 0 16px rgba(0, 0, 0, 0.4);
    }
  }

  input[type='file'] {
    position: absolute;
    left: -999em;
  }

  .click {
    position: relative;
    z-index: 1;
    font-weight: 700;
    text-decoration: underline;
    cursor: pointer;

    &:hover {
      text-decoration: none;
    }
  }
}

.card {
  background-color: $search-grey;
  border-radius: 0;
  border: none;
  padding: 1em;
}
