@import "~@angular/material/core/core";
@import "~@angular/material/core/theming/theming";
@import '~@angular/material/datepicker/datepicker-theme';
@import '~@angular/material/stepper/stepper-theme';
@import '~@angular/material/slider/slider-theme';
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

// Define a custom typography config that overrides the font-family as well as the
// `headlines` and `body-1` levels.
$custom-typography: mat-typography-config(
  $font-family: '"Kohinoor", "Open Sans", "Gill Sans MT", "Gill Sans", Corbel, Arial, sans-serif'
);
@include mat-base-typography($custom-typography);

@include mat-core($custom-typography);
// Palette generated with http://mcg.mbitson.com/#!?mcgpalette0=%236a955b
$taro-palette: (
  50 : #edf2eb,
  100 : #d2dfce,
  200 : #b5caad,
  300 : #97b58c,
  400 : #80a574,
  500 : #6a955b,
  600 : #628d53,
  700 : #578249,
  800 : #4d7840,
  900 : #3c672f,
  A100 : #c3ffb2,
  A200 : #9bff7f,
  A400 : #73ff4c,
  A700 : #5fff32,
  contrast: (
      50 : #000000,
      100 : #000000,
      200 : #000000,
      300 : #000000,
      400 : #000000,
      500 : #000000,
      600 : #ffffff,
      700 : #ffffff,
      800 : #ffffff,
      900 : #ffffff,
      A100 : #000000,
      A200 : #000000,
      A400 : #000000,
      A700 : #000000,
  )
);
$taro-theme-primary: mat-palette($taro-palette);
$taro-theme-accent:  mat-palette($mat-red);
$taro-theme-warn:  mat-palette($mat-red);

$taro-theme: mat-light-theme(
   $taro-theme-primary,
   $taro-theme-accent
);


// Create a theme
@include mat-core-theme($taro-theme);
@include mat-datepicker-theme($taro-theme);
@include mat-chips-theme($taro-theme);
@include mat-stepper-theme($taro-theme);
@include mat-icon-theme($taro-theme);
@include mat-slider-theme($taro-theme);

.mat-step-header .mat-step-icon-selected, .mat-step-header .mat-step-icon-state-done, .mat-step-header .mat-step-icon-state-edit, .mat-step-header .mat-step-icon {
  color: white;
}
