button {
  cursor: pointer;

  &:focus {
    outline: none;
  }

  &:disabled {
    cursor: not-allowed;
  }
}

.btn-single-icon {
  padding: 1.1em;
  padding: 0.8em;

  img {
    width: 1.4em;
  }
}

.row-toggle {
  border: none;
  background: transparent;

  > div {
    transform: scale(0.9);
    padding: 0.3em;
    background: #ccc;
    width: 1.6em;
    height: 1.6em;
    line-height: 0;
    border-radius: 50%;
    padding-top: 0.5em;
    transition: all 200ms cubic-bezier(0.250, 1.180, 0.340, 1.170);
    border: 1px solid transparent;
  }

  @media (hover: hover) {
    &:hover div {
      transform: scale(1.1);
      background-color: white;
      border-color: rgba(0, 0, 0, 0.16);
    }
  }

  &:active div {
    transform: scale(1.1);
    background-color: white;
    border-color: rgba(0, 0, 0, 0.16);
  }
}