.alert {
  font-weight: 600;
}

.alert.arrow-alert {
  position: relative;
  text-align: center;

  &:after {
    top: 100%;
    left: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: rgba(213, 0, 0, 0);
    border-width: 8px;
    margin-left: -8px;
  }

  &.alert-danger:after {
    border-top-color: #FFD9D5;
  }

  &.alert-info:after {
    border-top-color: #bee5eb;
  }
}