.hamburger-icon {
  font-size: 20px;
  width: 1em;
  height: 0.8em;
  position: relative;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;

  span {
    display: block;
    position: absolute;
    height: 0.13em;
    width: 100%;
    background: #424244;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: .25s ease-in-out;

    &:nth-child(1) {
      top: 0;
    }
    
    &:nth-child(2) {
      top: 0.4em;
    }
    
    &:nth-child(3) {
      top: 0.8em;
    }
  }

  &.open span:nth-child(1) {
    top: 0.3em;
    transform: rotate(135deg);
  }
  
  &.open span:nth-child(2) {
    opacity: 0;
    left: -1em;
  }
  
  &.open span:nth-child(3) {
    top: 0.3em;
    transform: rotate(-135deg);
  }
}